@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

header{width:100%;background-color:#070707;height:150px;text-align:center;vertical-align:middle}header h1{color:white;line-height:150px;font-size:45px}

footer{width:100%;background-color:#080808;height:85px;text-align:center}footer p{color:#f6fafa;line-height:85px;font-size:2rem;font-family:'Pacifico', cursive}

main{text-align:center}main div{background-color:#b0b6b5;height:200px;width:100%}main div form input[type='url']{width:50%;line-height:35px;margin-right:25px;margin-bottom:25px;margin-top:25px}main div form button{margin-top:25px;padding:5px 20px;font-size:25px;cursor:pointer}main div form button:hover{background-color:gray}main div form label{margin-right:25px;margin-left:10px;margin-bottom:25px;font-size:25px;cursor:pointer}main div table{margin:20px;height:250px;width:60%;background-color:#9c9898;display:inline-block;padding:25px;border-radius:15px;box-shadow:5px 10px #646363}main div table tr td{display:inline-block;margin-right:50px;font-size:25px}

.json-div #json-pretty{border-radius:15px;width:57%;margin:0 auto;box-shadow:5px 5px gray;padding:30px;margin-bottom:20px}

